import React, { useState } from 'react';

const WeatherReporter = () => {
  const ZIP_API_URL = "http://api.zippopotam.us/us/";
  const WEATHER_API_URL = "http://api.openweathermap.org/data/2.5/weather";
  const APPID = "789ca661d0a0b0e6fd2752420290b325";

  const [zipcode, setZipcode] = useState("");
  const [location, setLocation] = useState("");
  const [icon, setIcon] = useState(null);
  const [temp, setTemp] = useState("");
  const [windspeed, setWindspeed] = useState("");
  const [resultTime, setResultTime] = useState("");
  const [responseVisible, setResponseVisible] = useState(false);
  const [resultsVisible, setResultsVisible] = useState(false);
  const [errorVisible, setErrorVisible] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");

  const fetchLocationData = (evt) => {
    evt.preventDefault();

    if (zipcode === "") {
      handleError("No zipcode was given!");
      return;
    }

    fetch(ZIP_API_URL + zipcode)
      .then(checkStatus)
      .then(response => response.json())
      .then(fetchWeatherData)
      .catch(err => handleError("No location data for this zipcode!"));
  };

  const fetchWeatherData = (data) => {
    let firstMatch = data.places[0];
    const city = firstMatch["place name"];
    const state = firstMatch.state;
    const country = data["country abbreviation"];

    const query = `${WEATHER_API_URL}?APPID=${APPID}&units=imperial&q=${city},${state},${country}`;
    fetch(query)
      .then(checkStatus)
      .then(response => response.json())
      .then(processWeatherData)
      .catch(err => handleError("No weather data for this location!"));
  };

  const processWeatherData = (data) => {
    const city = data.name;
    setLocation(city);
    let iconUrl = "http://openweathermap.org/img/w/" + data.weather[0].icon + ".png";

    setIcon(<img src={iconUrl} alt={data.weather[0].main} />);

    const temp = data.main.temp;
    setTemp(temp);

    const wind = data.wind.speed + " mph";
    setWindspeed(wind);

    const dt = data.dt;
    setResultTime(Date(dt));

    setResponseVisible(true);
    setResultsVisible(true);
    setErrorVisible(false);
  };

  const handleError = (errMsg) => {
    setErrorMsg(errMsg);
    setResponseVisible(true);
    setResultsVisible(false);
    setErrorVisible(true);
  };

  const checkStatus = (response) => {
    if (!response.ok) {
      throw Error("Error in request: " + response.statusText);
    }
    return response;
  };

  return (
    <div className="flex flex-col bg-gray-100 rounded-md shadow-md">
      <header className="text-center py-2">
        <h1 className="text-xl font-semibold">Weather Reporter</h1>
        <h2 className="text-sm">Enter your zip code and find out what the weather is like!</h2>
      </header>
      <hr />
      <main className="flex-grow w-full md:w-1/2 place-self-center py-1">
        <form id="weather" onSubmit={fetchLocationData} className="flex justify-between">
          <label>
            Zipcode: <input type="text" id="zipcode" name="zipcode" value={zipcode} onChange={(e) => setZipcode(e.target.value)} className="mx-auto p-1 text-base sm:text-lg" />
          </label>
          <button type="submit" className="px-10 py-2 font-bold text-white bg-blue-500 hover:bg-blue-700">Get Weather</button>
        </form>
        {responseVisible && (
          <article id="response" className={errorVisible ? "hidden" : ""}>
            {errorVisible && <p id="error">{errorMsg}</p>}
            {resultsVisible && (
              <table id="results" className="w-full">
                <tbody>
                  <tr className="flex justify-between items-center">
                    <th id="location" className="text-xl font-bold">{location}</th>
                    <th id="icon">{icon}</th>
                  </tr>
                  <tr className="flex justify-between items-center">
                    <td>Temperature:</td>
                    <td>
                      <span id="temp">{temp}</span> &deg;F
                    </td>
                  </tr>
                  <tr className="flex justify-between items-center">
                    <td>Windspeed:</td>
                    <td id="windspeed">{windspeed}</td>
                  </tr>
                </tbody>
              </table>
            )}
            <p id="result-time">{resultTime}</p>
          </article>
        )}
      </main>
      <footer className="text-center text-sm italic">
        <hr />
        <p>
          Credit: Data from <a href="https://zippopotam.us/" className="text-blue-600">Zippopotam</a> and{" "}
          <a href="https://openweathermap.org/" className="text-blue-600">OpenWeather</a> APIs
        </p>
      </footer>
    </div>
  );
};

export default WeatherReporter;
