import React, { useState } from "react";
import { shiftCipher, customCipher } from '../misc/utils';

const MessageEncrypter = () => {
  const [inputText, setInputText] = useState("");
  const [cipherType, setCipherType] = useState("shift");
  const [key, setKey] = useState(1);
  const [textSize, setTextSize] = useState("text-base");
  const [allCaps, setAllCaps] = useState(false);
  const [result, setResult] = useState("");

  const handleChange = (event) => {
    setInputText(event.target.value);
  };

  const handleEncrypt = () => {
    let newResult = "";

    if (cipherType === "shift") {
      newResult = shiftCipher(inputText, key);
    } else if (cipherType === "custom") {
      newResult = customCipher(inputText);
    }

    setResult(newResult);
  };

  const handleReset = () => {
    setInputText("");
    setResult("");
  };

  const handleAllCaps = () => {
    setAllCaps(!allCaps);
  };

  const handleFontSize = (event) => {
    setTextSize(event.target.value);
  };

  return (
    <div className="bg-gray-100 rounded-md shadow-md">
      <div className="container mx-auto">
        <header className="py-2 text-center">
          <h1 className="text-xl font-semibold">
            Message Encrypter
          </h1>
          <h2 className="text-sm">
            Translate any text message into a super-secret cipher!
          </h2>
        </header>
        <hr />
        <main className="flex flex-wrap justify-center mt-5">
          <div className="w-full sm:w-1/3 p-5">
            <fieldset>
              <legend className="text-xl font-semibold">Text to Encrypt</legend>
              <textarea
                id="input-text"
                placeholder="Enter your message to encrypted here"
                value={inputText}
                onChange={handleChange}
                className="w-full h-32 p-4 bg-gray-100 border-2 border-gray-400 focus:border-blue-500 resize-none outline-none"
              ></textarea>
            </fieldset>
          </div>
          <div className="w-full sm:w-1/3 p-5">
            <fieldset>
              <legend className="text-xl font-semibold">
                Encrypt Options
              </legend>
              <p className="mb-2">
                <strong className="mr-2">Cipher Type:</strong>
                <select
                  id="cipher-type"
                  value={cipherType}
                  onChange={(e) => setCipherType(e.target.value)}
                  className="p-1"
                >
                  <option value="shift">Shift Cipher</option>
                  <option value="custom">Emoji Cipher</option>
                </select>
              </p>
              <p className="mb-2">
                <strong className="mr-2">Key:</strong>
                <input
                  type="number"
                  id="key"
                  name="key"
                  min="1"
                  max="26"
                  value={key}
                  onChange={(e) => setKey(parseInt(e.target.value))}
                  className="w-full p-2 bg-gray-100 border-2 border-gray-400"
                />
              </p>
              <p className="mb-2">
                <strong className="mr-2">Font Size:</strong>
                <label>
                  <input
                    checked={textSize === "text-base"}
                    type="radio"
                    name="text-size"
                    value="text-base"
                    onChange={handleFontSize}
                  />
                  Normal
                </label>
                <label className="ml-4">
                  <input
                    checked={textSize === "text-xl"}
                    type="radio"
                    name="text-size"
                    value="text-xl"
                    onChange={handleFontSize}
                  />
                  Large
                </label>
              </p>
              <p className="mb-4">
                <strong className="mr-2">All Caps:</strong>
                <input
                  type="checkbox"
                  id="all-caps"
                  checked={allCaps}
                  onChange={handleAllCaps}
                />
              </p>
              <button
                id="encrypt"
                onClick={handleEncrypt}
                className="w-1/2 py-2 mb-2 text-white bg-blue-500 hover:bg-blue-700 font-bold"
              >
                Encrypt
              </button>
              <button
                id="reset"
                onClick={handleReset}
                className="w-1/2 py-2 text-white bg-blue-500 hover:bg-blue-700 font-bold"
              >
                Reset
              </button>
            </fieldset>
          </div>
          <div className="w-full sm:w-1/3 p-5">
            <fieldset>
              <legend className="text-xl font-semibold">
                Encrypted Message
              </legend>
              <div
                id="result"
                className={`h-32 p-4 bg-gray-100 border-2 border-gray-400 overflow-auto ${
                  allCaps ? "uppercase" : ""
                } ${textSize}`}
              >
                {result}
              </div>
            </fieldset>
          </div>
        </main>
      </div>
    </div>
  );
}

export default MessageEncrypter;