import React, { useRef, useState } from "react";
import '/src/styles/minesweeper_styles.css';

const MinesweeperGame = () => {
  const [message, setMessage] = useState("");
  const [inputDisabled, setInputDisabled] = useState(false);
  const [dimensions, setDimensions] = useState({ width: 9, height: 9, mines: 10 });
  const gridRef = useRef();

  const initializeCell = (row, col) => {
    const cell = document.createElement("div");
    cell.classList.add("cell");
    cell.setAttribute("id", row + "_" + col);
    cell.addEventListener("click", makeMove);
    cell.addEventListener("contextmenu", (e) => {
      e.preventDefault();
      flagCell(e);
    });
    return cell;
  };

  const startGame = () => {
    const grid = gridRef.current;
    while (grid.firstChild) {
      grid.removeChild(grid.lastChild);
    }

    const rows = dimensions.height;
    const cols = dimensions.width;
    const mines = dimensions.mines;

    if (checkInputs(rows, cols, mines)) {
      setInputDisabled(true);
      const grid = gridRef.current;

      for (let c = 0; c < cols; c++) {
        const col = document.createElement("div");
        col.classList.add("row");

        for (let r = 0; r < rows; r++) {
          col.appendChild(initializeCell(r, c));
        }
        grid.appendChild(col);
      }
    }

    randomlyAssignMines();
  };

  const checkInputs = (nrows, ncols, nmines) => {
    if (nrows < 1 || ncols < 1 || nrows * ncols <= nmines) {
      setMessage("Please enter a valid number of rows/columns/mines!");
      return false;
    }
    setMessage("");
    return true;
  };

  const randomlyAssignMines = () => {
    const rows = dimensions.height;
    const cols = dimensions.width;
    const mines = dimensions.mines;
    let coords = [];
    let rand;
    let r;
    let c;

    let assignedMines = 0;

    while (assignedMines < mines) {
      rand = randomInt(rows * cols - 1);
      r = rand % rows;
      c = Math.floor(rand / rows);
      const idx = r + "_" + c;

      if (!coords.includes(idx)) {
        id(idx).classList.add("mine");
        coords.push(idx);
        assignedMines++;
      }
    }
  };

  const calculateNeighborMineCounts = (cellId) => {
    let count = 0;

    const neighbors = getNeighbors(cellId);
    neighbors.forEach((neighbor) => {
      if (id(neighbor).classList.contains("mine")) {
        count++;
      }
    });

    return count;
  };

  const makeMove = (event) => {
    const cell = event.currentTarget;
  
    if (!cell.classList.contains("opened")) {
      cell.classList.add("opened");
  
      if (!checkGameOver(cell)) {
        displayNeighbors(cell);
      } else {
        setInputDisabled(false);
      }
    }
  };

  const displayNeighbors = (cell) => {
    cell.classList.add("opened");
    if (cell.classList.contains("flagged")) {
      cell.classList.remove("flagged");
    }
    const mines = calculateNeighborMineCounts(cell.id);
  
    if (mines !== 0) {
      cell.textContent = mines;
    } else {
      setTimeout(() => {
        for (let neighbor of getNeighbors(cell.id)) {
          if (!id(neighbor).classList.contains("opened")) {
            displayNeighbors(id(neighbor));
          }
        }
      }, 200);
    }
  };

  const checkGameOver = (cell) => {
    if (cell.classList.contains("mine")) {
      cell.classList.add("opened");
      setMessage(`Game Over!`);
      return true;
    } else if (gameWon()) {
      setMessage(`Victory!`);
      return true;
    }
    return false;
  };

  const gameWon = () => {
    return Array.from(gridRef.current.getElementsByClassName("cell")).filter(cell => !cell.classList.contains("opened")).length === dimensions.mines;
  };
  

  const flagCell = (event) => {
    const cell = event.currentTarget;
    if (!cell.classList.contains("opened")) {
      if (cell.classList.contains("flagged")) {
        cell.classList.remove("flagged");
      } else {
        cell.classList.add("flagged");
      }
    }
  };

  const getNeighbors = (cellId) => {
    let ncols = dimensions.width; // ncols = width, nrows = height
    let nrows = dimensions.height;
    const cellID = ""+cellId;
    let cellRow = parseInt(cellID.split("_")[0]);
    let cellCol = parseInt(cellID.split("_")[1]);
    let neighborIds = [];
    for (let row = Math.max(cellRow - 1, 0); row <= Math.min(cellRow + 1, nrows - 1); row++) {
      for (let col = Math.max(cellCol - 1, 0); col <= Math.min(cellCol + 1, ncols - 1); col++) {
        let neighborId = row + "_" + col;
        neighborIds.push(neighborId);
      }
    }
    return neighborIds;
  };

  const randomInt = (max) => {
    // Math.floor causes upperbound to be exclusive
    return Math.floor(Math.random() * (max + 1));
  };

  const id = (idName) => {
    return document.getElementById(idName);
  };

  return (
    <div className="font-sans bg-gray-100 rounded-md shadow-md">
      <header className="py-2 text-center">
        <h1 className="text-xl font-mono font-semibold">Welcome to Minesweeper!</h1>
        <h2 className="text-sm font-mono">Left click to uncover, right click to flag.</h2>
      </header>
      <hr />
      <fieldset
        id="menu-options"
        className="flex flex-wrap justify-center space-x-0 sm:space-x-10 py-1"
      >
        <label className="w-1/2 sm:w-1/6 flex flex-col">
          Width:{" "}
          <input
            type="number"
            id="width"
            value={dimensions.width}
            onChange={(e) =>
              setDimensions({ ...dimensions, width: e.target.value })
            }
            disabled={inputDisabled}
            className="py-1"
          />
        </label>
        <label className="w-1/2 sm:w-1/6 flex flex-col">
          Height:{" "}
          <input
            type="number"
            id="height"
            value={dimensions.height}
            onChange={(e) =>
              setDimensions({ ...dimensions, height: e.target.value })
            }
            disabled={inputDisabled}
            className="py-1"
          />
        </label>
        <label className="w-1/2 sm:w-1/6 flex flex-col">
          Mines:{" "}
          <input
            type="number"
            id="mines"
            value={dimensions.mines}
            onChange={(e) =>
              setDimensions({ ...dimensions, mines: e.target.value })
            }
            disabled={inputDisabled}
            className="py-1"
          />
        </label>
        <button
          id="play"
          onClick={startGame}
          disabled={inputDisabled}
          className="py-1 text-base w-1/2 sm:w-1/6 text-white bg-blue-500 hover:bg-blue-700 font-bold mt-4 sm:mt-0"
        >
          Play
        </button>
      </fieldset>
      <hr />
      <div id="game-container" className="flex flex-col items-center">
        <div id="grid" ref={gridRef} className="my-12 flex"></div>
        <p id="result">{message}</p>
      </div>
    </div>
  );
};

export default MinesweeperGame;